<template>
<TabContent :background="background">
  <v-row style="padding: 10px; overflow-y: auto; max-height: 100%">
    <v-col cols="12" sm="6">
      <v-card>
        <v-card-title>Интерфейс</v-card-title>
        <v-card-text>
          <v-text-field v-model="background" label="Фоновое изображение"/>
          <v-btn @click="switchTheme">Сменить тему</v-btn>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col cols="12" sm="6">
      <v-card>
        <v-card-title>API и TKB</v-card-title>
        <v-card-text>
          <v-text-field v-model="tkbToken" label="Токен TKB"/>
          <v-text-field v-model="api.token" label="Токен BillManager"/>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col cols="12" sm="6">
      <v-card>
        <v-card-title>Служебный браузер</v-card-title>
        <v-card-text>
          <v-text-field v-model="browserSessionId" label="ID сессии"/>
          <v-text-field v-model="browserUrl" label="Ссылка"/>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="openBrowser">Открыть</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
    <v-col cols="12" sm="6">
      <v-card>
        <v-card-title>Маркус</v-card-title>
        <v-card-text>Редактировать и обучать бота Маркуса.</v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="() => tabMgr.open('Markus')">Открыть</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</TabContent>
</template>

<script>
import TabContent from '@/render/components/Common/TabContent.vue';

export default {
  components: { TabContent },
  data: () => ({
    background: localStorage.background,
    tkbToken: localStorage.tkbToken,
    browserSessionId: Math.floor(Math.random() * 1e9),
    browserUrl: 'https://ya.ru'
  }),

  watch: {
    background() {
      localStorage.background = this.background;
    },

    tkbToken() {
      localStorage.tkbToken = this.tkbToken;
    }
  },

  methods: {
    switchTheme() {
      const isDark = !this.$vuetify.theme.dark;
      this.$vuetify.theme.dark = isDark;
      this.$store.commit({ type: 'updateTheme', isDark });
    },

    openBrowser() {
      this.platform.openPanelWindow(this.browserUrl, this.browserSessionId);
    }
  }
};
</script>
